import $ from "../core";

// замещение контекта в элементе если content передан и получение содержимого элемента если content не передан
$.prototype.html = function (content) {
    for (let i = 0; i < this.length; i++) {
        if (content) {
            this[i].innerHTML = content;
        } else {
            return this[i].innerHTML;
        }
    }

    return this;
};

// получение определенного элемента на странице по номеру
$.prototype.eq = function (i) {
    const swap = this[i];
    const objLength = Object.keys(this).length;

    for (let i = 0; i < objLength; i++) {
        delete this[i];
    }

    this[0] = swap;
    this.length = 1;

    return this;
};

// получение номера элемента по порядку среди всех соседей с общим родителем
$.prototype.index = function () {
    const parent = this[0].parentNode;
    const childs = [...parent.children]; // из псевдомассива в массив

    const findMyIndex = (item) => {
        return item == this[0];
    };

    return childs.findIndex(findMyIndex);
};

// получение определенного элемента по селектору среди уже выбранных элементов
$.prototype.find = function (selector) {
    let numberOfItems = 0;
    let counter = 0;

    const copyObj = Object.assign({}, this);

    for (let i = 0; i < copyObj.length; i++) {
        const arr = copyObj[i].querySelectorAll(selector);
        if (arr.length == 0) {
            continue;
        }

        for (let j = 0; j < arr.length; j++) {
            this[counter] = arr[j];
            counter++;
        }

        numberOfItems += arr.length;
    }

    this.length = numberOfItems;
    const objLength = Object.keys(this).length;

    for (; numberOfItems < objLength; numberOfItems++) {
        delete this[numberOfItems];
    }
    return this;
};

//  определение ближайшего блока по заданному селектору
$.prototype.closest = function (selector) {
    let counter = 0;

    for (let i = 0; i < this.length; i++) {
        if (this[i].closest(selector) != null) {
            this[i] = this[i].closest(selector);
            counter++;
        } else {
            this[i] = '';
            counter++;
        }
    }
    const objLength = Object.keys(this).length;
    for (; counter < objLength; counter++) {
        delete this[counter];
    }
    return this;
};

// получение всех соседних элементов внутри родительского не включая сам элемент
$.prototype.siblings = function () {
    let numberOfItems = 0;
    let counter = 0;

    const copyObj = Object.assign({}, this); //создание копии объекта

    for (let i = 0; i < copyObj.length; i++) {
        const arr = copyObj[i].parentNode.children;
        for (let j = 0; j < arr.length; j++) {
            if (copyObj[i] === arr[j]) {
                continue;
            }
            this[counter] = arr[j];
            counter++;
        }
        numberOfItems += arr.length -1;
    }
    this.length = numberOfItems;
    const objLength = Object.keys(this).length;

    for (; numberOfItems < objLength; numberOfItems++) {
        delete this[numberOfItems];
    }
    return this;
};